import { GoogleLogin } from '@react-oauth/google';
import { jwtDecode } from "jwt-decode";
import supabase from '../api/Supabase';

const User = () => {
  async function login(objUser: any) {
    let {data} = await supabase.from('account').select('*').eq('email', objUser.email);
    if(data?.length == 0){
      let newUser = {
        'email': objUser.email,
        'name': objUser.name,
        'picture': objUser.picture
      }
      const { data, error } = await supabase
        .from('account')
        .insert([newUser])
        .select()
    }
  }

  return (
    <div>
      <GoogleLogin
        onSuccess={(credentialResponse:any) => {
          let objUser = jwtDecode(credentialResponse.credential);
          login(objUser);
        }}
        onError={() => {
          console.log('Login Failed');
        }}
      />
    </div>
  )
}

export default User;
